
import React from "react";
import "./../scss/login.scss";
import {hashHistory} from "react-router";
import MyAjax from "./../MyAjax.js";
import Toast from "./Toast.js";


export default class login extends React.Component{
	constructor(props){
		super(props);
	}

	toMenban(){
		window.history.go(-1);
	}
	
	toRegister(){
		hashHistory.push("/register");
	}

	toLogin(){
		var userID=this.refs.userID.value;
		var password=this.refs.password.value;
		console.log(userID,password);

		if(userID=="" || userID==""&&password==""){
			Toast.makeText("//",2500);
			$("#toast").css("background","red");

		}else{
			$("#UserBtn").attr("disabled","disabled");
			$("#UserBtn").html("...");
			var url ='http://datainfo.duapp.com/shopdata/userinfo.php?status=login&userID='+userID+'&password='+password;
			MyAjax.fetch(url,function(data){
				if(data=="0"){
					Toast.makeText("",2500);
					$("#toast").css("background","red");				

				}else if(data=="2"){
					Toast.makeText("",2500);
					$("#toast").css("background","red")
					
				}else{

					localStorage.setItem("isLogin","1");
					localStorage.setItem("userID",userID);
                	$("#toast").css("background","green")
					Toast.makeText("",2000);

					setTimeout(function(){

						hashHistory.push("/user")
						
					},3000)
					
					
					
				}
			},function(err){

				$("#loginBtn").removeAttr("disabled");
				$("loginBtn").val("");
				console.log(err);
			})
		}

	}

	render(){
		return(
			<div className="type">
				<header id="header">
					<div className="loginHeader">
						<div className="homeImg1" onClick={this.toMenban.bind(this)}>
							<i className="iconfont">&#xe615;</i>
						</div>						
						<div className="headerBox">
							
						</div>
						<div className="homeImg2">
							<span></span>
						</div>					
					</div>				
				</header>
				<div id="content" className="content">
					<div className="loginContent">
						<div className="loginIpt">
							<div className="UserBox">
								<p></p>
								<input type="text" ref="userID" placeholder="//"/>
							</div>
							<div className="UserBox">
								<p></p>
								<input type="password" ref="password" placeholder=""/>
							</div>
							<div className="UserBtn" id="UserBtn" onClick={this.toLogin.bind(this)}>								
								
							</div>
							<div className="loginReg">								
								<span>  </span>
								<span onClick={this.toRegister.bind(this)}>  </span>
							</div>
							<div className="loginQQ">								
								<img src="https://s10.mogucdn.com/p1/150803/upload_ieztmnzwmztdsoddgizdambqgyyde_210x210.png"/>
							</div>
						</div>
					</div>				
				</div>
			</div>
		)
	}
	
	componentDidUpdate(){
		
	}
}



